//modernizr
.no-webp .imagen-contacto {
    background-image: url('../img/encuentra.jpg');
}

.webp .imagen-contacto {
    background-image: url('../img/encuentra.webp');
}

.imagen-contacto 
{
    background-position: center center;  // util cuando es tablet y mobil
    background-size: cover;
    padding: $separacion*2 0;
    //centra los elementos
     display: flex;
     flex-direction: column;
     align-items: center; //

    h2 
    {
        font-size: 4rem;
        font-weight: $black;
        color: $blanco;
    }
    p 
    {
        font-size: 2.1rem;
        color: $blanco;
        text-align: center;
    }
}